html {
  font-size: 10px;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  background-color: rgb(244, 244, 244);
}
p {
  font-size: 1.6rem;
}
small {
  font-size: 1.2rem;
}
.container {
  margin: 0 auto;
  max-width: 1000px;
  padding: 40px;
}
.title {
  font-size: 4.4rem;
  text-align: center;
}
.form {
  display: grid;
}
.label {
  font-size: 1.2rem;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
  outline: none;
}
.input {
  font-size: 1.4rem;
  padding: 0.5rem 2rem;
  line-height: 2.8rem;
  border: 1px solid blueviolet;
  border-radius: 20px;
  outline: none;
}
.button {
  outline: none;
  background-color: rgba(0, 0, 0, 0.75);
  color: white;
  font-family: cursive;
  margin-top: 10px;
  padding: 1rem 2rem;
  border: 1px solid rgba(0, 0, 0, 0.75);
  border-radius: 20px;
  font-size: 1.4rem;
  cursor: pointer;
}
.button :hover {
  background-color: rgba(0, 0, 0, 0.85);
}
.card-list {
  margin-top: 3rem;
}
.card {
  padding: 2rem 4rem;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.25);
  margin-bottom: 2rem;
  background-color: white;
  display: flex;
}
.card-titlea {
  margin-bottom: 0.5rem;
  font-size: 3.2rem;
  text-align: center;
}
.card--image {
  margin: 0 auto;
}
.card--content {
  padding-left: 2rem;
}
.card--title-a {
  text-align: center;
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
}
.card--title-b {
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
}
